body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  width: 100%;
  height: 100%;
}

#root{
  width:100%;
  height:100%;
}
.main{
    display:grid;
    width:70%;
    height: 100%;
    grid-template-columns: 6.7% 6.7% 6.4% 1fr 19.8%;
    grid-template-rows: 4.65% 1fr 4.65%;
    border-right-width: 0.1em;
    border-right-color: gold;
    border-right-style: dotted;

}



.mobile .main{
    display:grid;
    width:0%;
    height: 0%;
    opacity: 0;
    font-size: 0;
}

.nameComponent{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-left: 2%;
    width: 100%;
    height:95%;
    grid-column: 4 / 5;
    grid-row: 2 / 3; 
    text-align: center;
}


.bigName{
    font-family: 'DAYROM';
    font-size: 8em;
}

.smallName{
    font-family: 'DAYROM';
    font-size: 4em;
}

.nav{
    width: 30%;
    height: 100%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.mobile .nav{
    width: 100%;
    height: 100%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.navItemContainer{
    width:100%;
    height: 25%;
}

.navitem{
    width:100%;
    text-align: right;
    height: 100%;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content:center;
            justify-content:center;
    background: white;
    border-style: none;
    outline: none;
    cursor: pointer;
    transition-property: background, color; 
    transition-duration: 0.15s;
}

.navsleep{
    color:gainsboro !important;
}

.navitem:hover{
    background:gold;
    color: white !important;
    
}

.blog{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.blog .title{
    font-family: Coolvetica;
    font-size: 4em;
    color: #1F1F1F;
}

.blogtext{
    font-family: Coolvetica;
    font-size: 1.5em;
    white-space: pre-wrap;
    color: #1F1F1F
}

.blog .date{
    font-family: Coolvetica;
    font-size: 1em;
    color: lightgrey;
    margin-top:-0.5em;
    margin-bottom: 1.5em;
}

img{
    width: 100%;
    height: 20em;
    object-fit: cover;
}

sup{
    color: darkred;
    font-size: 0.6em !important;
}

.bbar{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    grid-column: 1 / 4;
    grid-row: 3 / 4;
}

.button{
    margin-top: 0.25em;
    margin-left: 0.3em;
}


.dNavContainer{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    top: -1px;
    background-color: white;
}

.dNavContainer .alive{
    cursor: pointer;
    transition-property: color; 
    transition-duration: 0.15s;
}

.dNavContainer .item:hover{
    color:#1F1F1F;
}

.dNavContainer .item{
    font-family: 'DAYROM';
    font-size: 1.5em;
    transition-property: color; 
    transition-duration: 0.15s;
}

@font-face {
    font-family: 'DAYROM';
    src: url(/bry/static/media/DAYROM.b03a9848.woff) format('woff');
    font-weight: normal;
    font-style: normal;
  
}


@font-face {
    font-family: 'Coolvetica';
    src: url("/bry/static/media/coolvetica rg.247ed841.woff") format('woff');
    font-weight: normal;
    font-style: normal;
  
}
.content{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 100%;
  font-family: 'DAYROM';
}

.page{
  height: 100%;
  width: 100%;
}

@font-face {
  font-family: 'DAYROM';
  src: url(/bry/static/media/DAYROM.b03a9848.woff) format('woff');
  font-weight: normal;
  font-style: normal;

}


